import React, { Component } from "react";
import Cookies from "js-cookie";
import ConfirmButton from "../ConfirmButton/ConfirmButton";
import Axios from "axios";
import NavigationBar from "../NavigationBar/NavigationBar";

class Torles extends Component {
	state = {
		id: "",
		currentUser: Cookies.get("UsrId"),
		reset: false,
	};
	handleSubmit = () => {
		let requestOption = {
			method: "DELETE",
			headers: { "Content-Type": "application/json" },
		};
		fetch(`http://tomimedia.duckdns.org:3000/tasks/${this.state.id}`, requestOption)
			.then((res) => res.json())
			.then((data) => {
				this.setState({
					id: "",
					reset: true,
				});
				window.location.reload();
			});
	};
	componentDidMount() {
		if (this.state.currentUser === "osztály") {
			fetch(
				`http://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
			)
				.then((res1) => res1.json())
				.then(
					(result) => {
						this.setState({
							isLoaded: true,
							hazik: result,
						});
					},
					(error) => {
						this.setState({
							isLoaded: true,
							error,
						});
					}
				);
		} else if (this.state.currentUser === "") {
			Cookies.set("UsrId", "osztály");
			this.setState({
				currentUser: "osztály",
			});
			fetch(
				`http://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
			)
				.then((res1) => res1.json())
				.then(
					(result) => {
						this.setState({
							isLoaded: true,
							hazik: result,
						});
					},
					(error) => {
						this.setState({
							isLoaded: true,
							error,
						});
					}
				);
		} else {
			let requestOptions = {
				headers: {
					"Access-Token": this.state.currentUser,
				},
			};
			Axios.get(
				"http://tomimedia.duckdns.org:3002/getCurrentUser",
				requestOptions
			).then((data) => {
				console.log(data);
				this.setState({
					currentUser: data.data.user,
				});
				fetch(
					`http://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
				)
					.then((res1) => res1.json())
					.then(
						(result) => {
							this.setState({
								isLoaded: true,
								hazik: result,
							});
						},
						(error) => {
							this.setState({
								isLoaded: true,
								error,
							});
						}
					);
			});
		}
	}
	handleIdChange = (e) => {
		this.setState({
			id: e.target.value,
		});
	};
	render() {
		let listItems = (
			<tr>
				<td></td>
			</tr>
		);
		if (this.state.error) {
			listItems = (
				<tr>
					<td>Hiba: {this.state.error.message}</td>
				</tr>
			);
		} else if (!this.state.isLoaded) {
			listItems = (
				<tr>
					<td>Betöltés...</td>
				</tr>
			);
		} else {
			listItems = this.state.hazik.map((hazi) => (
				<tr key={hazi.key}>
					<td>{hazi._id}</td>
					<td>{hazi.targy}</td>
					<td>{hazi.leiras}</td>
					<td>{hazi.kezdete}</td>
					<td>{hazi.lejarat}</td>
					<td>{hazi.status}</td>
					<td>{hazi.userId}</td>
				</tr>
			));
		}
		return (
			<div>
				<NavigationBar page="torles" />
				<div className="container mt-5">
					<h2>Házi törlése</h2>
					<form className="form-grid">
						<div className="form-row">
							<input
								type="text"
								name="id"
								id="_id"
								placeholder="Id"
								autoComplete="off"
								required
								value={this.state.id}
								onChange={this.handleIdChange}
								className="form-control bg-light"
							/>
						</div>
						<br />
						<ConfirmButton
							name="delete"
							label="Törlés"
							question="Biztosan törölni szeretnéd ezt a házit?"
							className="btn-danger"
							onClick={this.handleSubmit}
						/>
					</form>
					<table className="table table-dark table-striped table-responsive mt-5">
						<thead className="thead-dark">
							<tr>
								<th scope="col">ID</th>
								<th scope="col">Tárgy</th>
								<th scope="col">Leírás</th>
								<th scope="col">Kezdete</th>
								<th scope="col">Lejárat</th>
								<th scope="col">Státusz</th>
								<th scope="col">Felh azon</th>
							</tr>
						</thead>
						<tbody>{listItems}</tbody>
					</table>
				</div>
			</div>
		);
	}
}

export default Torles;
