import React, { Component } from "react";
import Cookies from "js-cookie";
import { Redirect } from "react-router-dom";

class Logout extends Component {
	state = {
		ready: false,
	};
	setCookie = () => {
		Cookies.set("UsrId", "osztály");
		this.setState({
			ready: true,
		});
	};
	render() {
		this.setCookie();
		return (
			<div>
				<h1>Kijelentkezés</h1>
				<Redirect to="/" />
			</div>
		);
	}
}

export default Logout;
