import React, { Component } from "react";
import Cookies from "js-cookie";
import Axios from "axios";
import NavigationBar from "../NavigationBar/NavigationBar";

export class Hozzaadas extends Component {
	state = {
		currentUser: Cookies.get("UsrId"),
		error: null,
		isLoaded: false,
		hazik: [],
		targy: "",
		leiras: "",
		kezdete: "",
		lejarata: "",
		statusa: "várakozik",
	};
	componentDidMount() {
		if (this.state.currentUser === "osztály") {
			fetch(
				`http://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
			)
				.then((res1) => res1.json())
				.then(
					(result) => {
						this.setState({
							isLoaded: true,
							hazik: result,
						});
					},
					(error) => {
						this.setState({
							isLoaded: true,
							error,
						});
					}
				);
		} else if (this.state.currentUser === "") {
			Cookies.set("UsrId", "osztály");
			this.setState({
				currentUser: "osztály",
			});
			fetch(
				`http://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
			)
				.then((res1) => res1.json())
				.then(
					(result) => {
						this.setState({
							isLoaded: true,
							hazik: result,
						});
					},
					(error) => {
						this.setState({
							isLoaded: true,
							error,
						});
					}
				);
		} else {
			let requestOptions = {
				headers: {
					"Access-Token": this.state.currentUser,
				},
			};
			Axios.get(
				"http://tomimedia.duckdns.org:3002/getCurrentUser",
				requestOptions
			).then((data) => {
				console.log(data);
				this.setState({
					currentUser: data.data.user,
				});
				fetch(
					`http://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
				)
					.then((res1) => res1.json())
					.then(
						(result) => {
							this.setState({
								isLoaded: true,
								hazik: result,
							});
						},
						(error) => {
							this.setState({
								isLoaded: true,
								error,
							});
						}
					);
			});
		}
	}
	handleTargyChange = (e) => {
		this.setState({ targy: e.target.value });
	};
	handleLeirasChange = (e) => {
		this.setState({ leiras: e.target.value });
	};
	handleKezdetChange = (e) => {
		this.setState({ kezdete: e.target.value });
	};
	handleLejaratChange = (e) => {
		this.setState({ lejarata: e.target.value });
	};
	handleStatusChange = (e) => {
		this.setState({ statusa: e.target.value });
	};
	handleSubmit = (e) => {
		e.preventDefault();
		this.setState({
			targy: "",
			leiras: "",
			kezdete: "",
			lejarata: "",
		});
		let { targy, leiras, kezdete, lejarata, statusa } = this.state;
		let requestOption = {
			method: "POST",
			headers: { "Content-Type": "application/json" },
			body: JSON.stringify({
				status: statusa,
				userId: Cookies.get("UsrId"),
				targy,
				leiras,
				kezdete,
				lejarat: lejarata,
			}),
		};
		fetch("http://tomimedia.duckdns.org:3000/tasks", requestOption)
			.then((res) => res.json())
			.then((data) => {
				console.log(data);
				window.location.reload();
			})
			.catch((error) => console.error(error));
	};
	render() {
		// let hazik = [
		// 	{
		// 		_id: "2837462384",
		// 		targy: "Matek",
		// 		leiras: "Valami házi izé",
		// 		kezdete: new Date(Date.now()),
		// 		lejarat: new Date(Date.now()),
		// 		status: "várakozik",
		// 		userId: "osztály",
		// 	},
		// 	{
		// 		_id: "2837462384",
		// 		targy: "Matek",
		// 		leiras: "Valami házi izé",
		// 		kezdete: new Date(Date.now()),
		// 		lejarat: new Date(Date.now()),
		// 		status: "várakozik",
		// 		userId: "osztály",
		// 	},
		// ];
		let listItems = this.state.hazik.map((hazi) => (
			<tr key={hazi._id}>
				<td>{hazi._id}</td>
				<td>{hazi.targy}</td>
				<td>{hazi.leiras}</td>
				<td>{hazi.kezdete}</td>
				<td>{hazi.lejarat}</td>
				<td>{hazi.status}</td>
				<td>{hazi.userId}</td>
			</tr>
		));
		return (
			<div>
				<NavigationBar page="hozzaadas" />
				<main>
					<div className="container mt-5">
						<h2>Házi felvétele</h2>
						<form
							onSubmit={this.handleSubmit}
							className="form-grid"
						>
							<div className="form-row">
								<div className="col">
									<input
										type="text"
										name="targy"
										id="targy"
										value={this.state.targy}
										onChange={this.handleTargyChange}
										autoComplete="off"
										placeholder="Tárgy"
										className="form-control mb-2 bg-light"
										required
									/>
								</div>
								<div className="col">
									<input
										type="text"
										name="leiras"
										id="leiras"
										value={this.state.leiras}
										onChange={this.handleLeirasChange}
										autoComplete="off"
										placeholder="Leírás"
										className="form-control bg-light"
										required
									/>
								</div>
							</div>
							<div className="form-row">
								<div className="col">
									<input
										type="text"
										name="kezdete"
										id="kezdete"
										value={this.state.kezdete}
										onChange={this.handleKezdetChange}
										autoComplete="off"
										placeholder="Kezdete (kötőjellel elválasztva)"
										className="form-control mb-2 bg-light"
										required
									/>
								</div>
								<div className="col">
									<input
										type="text"
										name="lejarat"
										id="lejarat"
										value={this.state.lejarata}
										onChange={this.handleLejaratChange}
										autoComplete="off"
										placeholder="Lejárata (kötőjellel elválasztva)"
										className="form-control mb-2 bg-light"
										required
									/>
								</div>
							</div>
							<fieldset
								className="form-group"
								onChange={this.handleStatusChange}
							>
								<div className="row">
									<legend className="col-form-label col-sm-2 pt-0">
										Státusz
									</legend>
									<div className="col-sm-10">
										<div className="form-check">
											<input
												className="form-check-input"
												type="radio"
												name="gridRadios"
												id="gridRadios1"
												value="várakozik"
												checked={
													this.state.statusa ===
													"várakozik"
												}
											/>
											<label
												className="form-check-label"
												for="gridRadios1"
											>
												Várakozik
											</label>
										</div>
										<div className="form-check">
											<input
												className="form-check-input"
												type="radio"
												name="gridRadios"
												id="gridRadios2"
												value="folyamatban"
												checked={
													this.state.statusa ===
													"folyamatban"
												}
											/>
											<label
												className="form-check-label"
												for="gridRadios2"
											>
												Folyamatban
											</label>
										</div>
										<div className="form-check disabled">
											<input
												className="form-check-input"
												type="radio"
												name="gridRadios"
												id="gridRadios3"
												value="kész"
												checked={
													this.state.statusa ===
													"kész"
												}
											/>
											<label
												className="form-check-label"
												for="gridRadios3"
											>
												Kész
											</label>
										</div>
									</div>
								</div>
							</fieldset>
							<input
								type="submit"
								value="Hozzáadás"
								className="btn btn-success mb-5"
							/>
						</form>
						<table className="table table-dark table-striped table-responsive">
							<thead className="thead-dark">
								<tr>
									<th scope="col">ID</th>
									<th scope="col">Tárgy</th>
									<th scope="col">Leírás</th>
									<th scope="col">Kezdete</th>
									<th scope="col">Lejárat</th>
									<th scope="col">Státusz</th>
									<th scope="col">Felh. azon</th>
								</tr>
							</thead>
							<tbody>{listItems}</tbody>
						</table>
					</div>
				</main>
			</div>
		);
	}
}

export default Hozzaadas;
