import React, { Component, Fragment } from "react";
import { Navbar, Nav, NavDropdown } from "react-bootstrap";
import Axios from "axios";
import Cookies from "js-cookie";

export class NavigationBar extends Component {
	state = {
		currentUser: Cookies.get("UsrId"),
		loaded: false,
	};
	componentDidMount() {
		if (this.state.currentUser === "") {
			Cookies.set("UsrId", "osztály");
			this.setState({
				currentUser: "osztály",
				loaded: true,
			});
		} else if (this.state.currentUser === "osztály") {
			this.setState({
				loaded: true,
			});
		} else {
			let requestOptions = {
				headers: {
					"Access-Token": this.state.currentUser,
				},
			};
			Axios.get(
				"http://tomimedia.duckdns.org:3002/getCurrentUser",
				requestOptions
			).then((data) => {
				console.log(data);
				this.setState({
					currentUser: data.data.user,
					loaded: true,
				});
			});
		}
	}
	render() {
		return (
			<div>
				{this.state.loaded ? (
					<Fragment>
						<Navbar bg="light" expand="lg">
							<Navbar.Brand href="#home">
								Házi feladat nyilvántartó
							</Navbar.Brand>
							<Navbar.Toggle aria-controls="basic-navbar-nav" />
							<Navbar.Collapse id="basic-navbar-nav">
								<Nav className="mr-auto">
									{this.props.page === "fooldal" ? (
										<Nav.Link
											href="/index"
											className="active"
										>
											Főoldal
										</Nav.Link>
									) : (
										<Nav.Link href="/index" className="">
											Főoldal
										</Nav.Link>
									)}

									{this.props.page === "hozzaadas" ? (
										<Nav.Link
											href="/hozzaadas"
											className="active"
										>
											Hozzáadás
										</Nav.Link>
									) : (
										<Nav.Link
											href="/hozzaadas"
											className=""
										>
											Hozzáadás
										</Nav.Link>
									)}
									{this.state.currentUser ===
									"Németh Tamás" ? (
										<div>
											{this.props.page === "modositas" ? (
												<Nav.Link
													href="/modositas"
													className="active"
												>
													Módosítás
												</Nav.Link>
											) : (
												<Nav.Link
													href="/modositas"
													className=""
												>
													Módosítás
												</Nav.Link>
											)}
										</div>
									) : (
										<div>
											{this.props.page ===
											"modositasuser" ? (
												<Nav.Link
													href="/modositasuser"
													className="active"
												>
													Módosítás
												</Nav.Link>
											) : (
												<Nav.Link
													href="/modositasuser"
													className=""
												>
													Módosítás
												</Nav.Link>
											)}
										</div>
									)}
									{this.props.page === "masolas" ? (
										<Nav.Link
											href="/masolas"
											className="active"
										>
											Másolás
										</Nav.Link>
									) : (
										<Nav.Link href="/masolas" className="">
											Másolás
										</Nav.Link>
									)}
									{this.props.page === "torles" ? (
										<Nav.Link
											href="/torles"
											className="active"
										>
											Törlés
										</Nav.Link>
									) : (
										<Nav.Link href="/torles" className="">
											Törlés
										</Nav.Link>
									)}
									<NavDropdown
										title="Fiók kezelés"
										id="basic-nav-dropdown"
									>
										{this.state.loaded ? (
											this.state.currentUser ===
											"osztály" ? (
												<Fragment>
													{this.props.page ===
													"login" ? (
														<NavDropdown.Item
															href="/login"
															className="active"
														>
															Bejelentkezés
														</NavDropdown.Item>
													) : (
														<NavDropdown.Item
															href="/login"
															className=""
														>
															Bejelentkezés
														</NavDropdown.Item>
													)}
													<NavDropdown.Divider />
													{this.props.page ===
													"register" ? (
														<NavDropdown.Item
															href="/register"
															className="active"
														>
															Regisztráció
														</NavDropdown.Item>
													) : (
														<NavDropdown.Item
															href="/register"
															className=""
														>
															Regisztráció
														</NavDropdown.Item>
													)}
												</Fragment>
											) : (
												<Fragment>
													<NavDropdown.Item href="/logout">
														Kijelentkezés
													</NavDropdown.Item>
												</Fragment>
											)
										) : (
											""
										)}
									</NavDropdown>
								</Nav>
							</Navbar.Collapse>
						</Navbar>
					</Fragment>
				) : (
					""
				)}
			</div>
		);
	}
}

export default NavigationBar;
