import React, { Component, Fragment } from "react";
import Cookies from "js-cookie";
import { Redirect } from "react-router-dom";
import Axios from "axios";
import NavigationBar from "../NavigationBar/NavigationBar";

class Modositas extends Component {
	state = {
		currentUser: Cookies.get("UsrId"),
		id: "",
		userId: "",
		targy: "",
		leiras: "",
		kezdete: "",
		lejarat: "",
		status: "várakozik",
		hazik: [],
		isLoaded: false,
	};
	handleIdChange = (e) => {
		this.setState({
			id: e.target.value,
		});
	};
	handleUserIdChange = (e) => {
		this.setState({
			userId: e.target.value,
		});
	};
	handleTargyChange = (e) => {
		this.setState({
			targy: e.target.value,
		});
	};
	handleLeirasChange = (e) => {
		this.setState({
			leiras: e.target.value,
		});
	};
	handleKezdeteChange = (e) => {
		this.setState({
			kezdete: e.target.value,
		});
	};
	handleLejaratChange = (e) => {
		this.setState({
			lejarat: e.target.value,
		});
	};
	handleStastusChange = (e) => {
		this.setState({
			status: e.target.value,
		});
	};
	handleSubmit = async (e) => {
		e.preventDefault();
		console.log(this.state.status);
		let requestOption = {
			method: "POST",
			headers: { "Content-Type": "application/json" },
			body: JSON.stringify({
				id: this.state.id,
				targy: this.state.targy,
				leiras: this.state.leiras,
				kezdete: this.state.kezdete,
				lejarat: this.state.lejarat,
				gridRadios: this.state.status,
				usrId: this.state.userId,
			}),
		};
		console.log(requestOption);
		fetch("http://tomimedia.duckdns.org:3002/updateTask", requestOption)
			.then((res) => res.json())
			.then((data) => {
				window.location.reload();
			});
	};
	componentDidMount() {
		if (this.state.currentUser === "osztály") {
			fetch(
				`http://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
			)
				.then((res1) => res1.json())
				.then(
					(result) => {
						this.setState({
							isLoaded: true,
							hazik: result,
						});
					},
					(error) => {
						this.setState({
							isLoaded: true,
							error,
						});
					}
				);
		} else if (this.state.currentUser === "") {
			Cookies.set("UsrId", "osztály");
			this.setState({
				currentUser: "osztály",
			});
			fetch(
				`http://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
			)
				.then((res1) => res1.json())
				.then(
					(result) => {
						this.setState({
							isLoaded: true,
							hazik: result,
						});
					},
					(error) => {
						this.setState({
							isLoaded: true,
							error,
						});
					}
				);
		} else {
			let requestOptions = {
				headers: {
					"Access-Token": this.state.currentUser,
				},
			};
			Axios.get(
				"http://tomimedia.duckdns.org:3002/getCurrentUser",
				requestOptions
			).then((data) => {
				console.log(data);
				this.setState({
					currentUser: data.data.user,
				});
				fetch(
					`http://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
				)
					.then((res1) => res1.json())
					.then(
						(result) => {
							this.setState({
								isLoaded: true,
								hazik: result,
							});
						},
						(error) => {
							this.setState({
								isLoaded: true,
								error,
							});
						}
					);
			});
		}
	}
	render() {
		let listItems = this.state.hazik.map((hazi) => (
			<tr key={hazi._id}>
				<td>{hazi._id}</td>
				<td>{hazi.targy}</td>
				<td>{hazi.leiras}</td>
				<td>{hazi.kezdete}</td>
				<td>{hazi.lejarat}</td>
				<td>{hazi.status}</td>
				<td>{hazi.userId}</td>
			</tr>
		));
		return (
			<div>
				<NavigationBar page="modositas" />
				{this.state.isLoaded ? (
					this.state.currentUser === "Németh Tamás" ? (
						<div className="container mt-5">
							<h2>Házi módosítása</h2>
							<form
								className="form-grid"
								onSubmit={this.handleSubmit}
							>
								<div className="form-row">
									<div className="col">
										<input
											type="text"
											name="id"
											id="_id"
											placeholder="Id"
											autoComplete="off"
											className="form-control mb-2 bg-light"
											value={this.state.id}
											onChange={this.handleIdChange}
											required
										/>
									</div>
									<div className="col">
										<input
											type="text"
											name="userId"
											id="userId"
											placeholder="Felhasználói azonosító"
											autoComplete="off"
											className="form-control mb-2 bg-light"
											value={this.state.userId}
											onChange={this.handleUserIdChange}
										/>
									</div>
								</div>
								<div className="form-row">
									<div className="col">
										<input
											type="text"
											name="targy"
											id="targy"
											placeholder="Tárgy"
											autoComplete="off"
											className="form-control mb-2 bg-light"
											value={this.state.targy}
											onChange={this.handleTargyChange}
										/>
									</div>
									<div className="col">
										<input
											type="text"
											name="leiras"
											id="leiras"
											placeholder="Leírás"
											autoComplete="off"
											className="form-control mb-2 bg-light"
											value={this.state.leiras}
											onChange={this.handleLeirasChange}
										/>
									</div>
								</div>
								<div className="form-row">
									<div className="col">
										<input
											type="text"
											name="kezdet"
											id="kezdet"
											placeholder="Kezdete (kötőjellel elválasztva)"
											autoComplete="off"
											className="form-control mb-2 bg-light"
											value={this.state.kezdete}
											onChange={this.handleKezdeteChange}
										/>
									</div>
									<div className="col">
										<input
											type="text"
											name="lejarat"
											id="lejarat"
											placeholder="Lejárata (kötőjellel elválasztva)"
											autoComplete="off"
											className="form-control mb-2 bg-light"
											value={this.state.lejarat}
											onChange={this.handleLejaratChange}
										/>
									</div>
								</div>
								<fieldset
									onChange={this.handleStastusChange}
									class="form-group"
								>
									<div className="row">
										<legend className="col-form-label col-sm-2 pt-0">
											Státusz
										</legend>
										<div className="col-sm-10">
											<div className="form-check">
												<input
													className="form-check-input"
													type="radio"
													name="gridRadios"
													id="gridRadios1"
													value="várakozik"
													checked={
														this.state.status ===
														"várakozik"
													}
												/>
												<label
													className="form-check-label"
													for="gridRadios1"
												>
													Várakozik
												</label>
											</div>
											<div className="form-check">
												<input
													className="form-check-input"
													type="radio"
													name="gridRadios"
													id="gridRadios2"
													value="folyamatban"
													checked={
														this.state.status ===
														"folyamatban"
													}
												/>
												<label
													className="form-check-label"
													for="gridRadios2"
												>
													Folyamatban
												</label>
											</div>
											<div className="form-check disabled">
												<input
													className="form-check-input"
													type="radio"
													name="gridRadios"
													id="gridRadios3"
													value="kész"
													checked={
														this.state.status ===
														"kész"
													}
												/>
												<label
													className="form-check-label"
													for="gridRadios3"
												>
													Kész
												</label>
											</div>
										</div>
									</div>
								</fieldset>
								<input
									type="submit"
									value="Módosítás"
									className="btn btn-warning mb-5"
								/>
							</form>
							<table className="table table-dark table-striped table-responsive">
								<thead className="thead-dark">
									<tr>
										<th scope="col">ID</th>
										<th scope="col">Tárgy</th>
										<th scope="col">Leírás</th>
										<th scope="col">Kezdete</th>
										<th scope="col">Lejárat</th>
										<th scope="col">Státusz</th>
										<th scope="col">Felh. azon</th>
									</tr>
								</thead>
								<tbody>{listItems}</tbody>
							</table>
						</div>
					) : (
						<Redirect to="modositasuser" />
					)
				) : (
					<Fragment>
						<div className="container">
							<div className="row">
								<div className="col-md-3 col-sm-12"></div>
								<div className="col-md-6 col-sm-12">
									<h1 className="text-warning">
										Betöltés...
									</h1>
								</div>
								<div className="col-md-3 col-sm-12"></div>
							</div>
						</div>
					</Fragment>
				)}
			</div>
		);
	}
}

export default Modositas;
