import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import NavigationBar from "../NavigationBar/NavigationBar";

class JelszoVisszaalitasa extends Component {
	state = {
		id: this.props.match.params.userId,
		error: "",
		pass1: "",
		pass2: "",
		done: false,
	};
	handlePass1Change = (e) => {
		this.setState({
			pass1: e.target.value,
		});
	};
	handlePass2Change = (e) => {
		this.setState({
			pass2: e.target.value,
		});
	};
	handleSubmit = (e) => {
		e.preventDefault();
		let requestOption = {
			method: "POST",
			headers: { "Content-Type": "application/json" },
			body: JSON.stringify({
				userid: this.state.id,
				pwd1: this.state.pass1,
				pwd2: this.state.pass2,
			}),
		};
		fetch("http://tomimedia.duckdns.org:3002/reset", requestOption)
			.then((res) => res.json())
			.then((data) => {
				if (data.reset === "failed") {
					this.setState({
						error: data.error,
						pass1: "",
						pass2: "",
					});
				} else {
					this.setState({
						error: "",
						pass1: "",
						pass2: "",
						done: true,
					});
				}
			});
	};
	render() {
		return (
			<div>
				<NavigationBar />
				{this.state.done ? (
					<Redirect to="/login" />
				) : (
					<div className="container-fluid">
						<div className="row">
							<div className="col-md-3 col-sm-12"></div>
							<div className="col-md-6 col-sm-12">
								<h4 className="mt-5 text-info">
									Jelszó visszaállítása
								</h4>
								{this.state.error !== "" ? (
									<h4 className="mt-2 text-danger">
										{this.state.error}
									</h4>
								) : (
									""
								)}
								<form onSubmit={this.handleSubmit}>
									<div className="form-group">
										<label
											htmlFor="userid"
											className="mt-2"
										>
											Azonosító
										</label>
										<input
											type="text"
											name="userid"
											id="userid"
											placeholder="Felhasználói Azonosító"
											autoComplete="off"
											disabled
											value={this.state.id}
											required
											className="form-control bg-light"
										/>
										<label htmlFor="pwd1" className="mt-2">
											Jelszó
										</label>
										<input
											type="password"
											name="pwd1"
											id="pwd1"
											placeholder="Jelszó"
											autoComplete="off"
											value={this.state.pass1}
											required
											className="form-control bg-light"
											onChange={this.handlePass1Change}
										/>
										<label htmlFor="pwd2" className="mt-2">
											Jelszó mégegyszer
										</label>
										<input
											type="password"
											name="pwd2"
											id="pwd2"
											placeholder="Jelszó mégegyszer"
											autoComplete="off"
											value={this.state.pass2}
											required
											className="form-control bg-light"
											onChange={this.handlePass2Change}
										/>
										<input
											type="submit"
											value="Elküld"
											className="btn btn-info mt-1"
										/>
									</div>
								</form>
							</div>
							<div className="col-md-3 col-sm-12"></div>
						</div>
					</div>
				)}
			</div>
		);
	}
}

export default JelszoVisszaalitasa;
