import React, { Component } from "react";
import Cookies from "js-cookie";
import Axios from "axios";
import NavigationBar from "../NavigationBar/NavigationBar";

class Masolas extends Component {
	state = {
		currentUser: Cookies.get("UsrId"),
		error: "",
		azon: "",
	};
	handleAzonChange = (e) => {
		this.setState({
			azon: e.target.value,
		});
	};
	handleSubmit = (e) => {
		e.preventDefault();
		let requestOption = {
			method: "POST",
			headers: { "Content-Type": "application/json" },
			body: JSON.stringify({
				id: this.state.azon,
				currentUser: this.state.currentUser,
			}),
		};
		fetch("http://tomimedia.duckdns.org:3002/copyTask", requestOption)
			.then((res) => res.json())
			.then((data) => {
				if (data.copy === "failed") {
					this.setState({
						error: data.error,
					});
				}
				this.setState({
					azon: "",
				});
			})
			.catch((err) => {
				console.log(err);
			});
	};
	componentDidMount() {
		if (this.state.currentUser === "osztály") {
			fetch(
				`http://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
			)
				.then((res1) => res1.json())
				.then(
					(result) => {
						this.setState({
							isLoaded: true,
							hazik: result,
						});
					},
					(error) => {
						this.setState({
							isLoaded: true,
							error,
						});
					}
				);
		} else if (this.state.currentUser === "") {
			Cookies.set("UsrId", "osztály");
			this.setState({
				currentUser: "osztály",
			});
			fetch(
				`http://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
			)
				.then((res1) => res1.json())
				.then(
					(result) => {
						this.setState({
							isLoaded: true,
							hazik: result,
						});
					},
					(error) => {
						this.setState({
							isLoaded: true,
							error,
						});
					}
				);
		} else {
			let requestOptions = {
				headers: {
					"Access-Token": this.state.currentUser,
				},
			};
			Axios.get(
				"http://tomimedia.duckdns.org:3002/getCurrentUser",
				requestOptions
			).then((data) => {
				console.log(data);
				this.setState({
					currentUser: data.data.user,
				});
				fetch(
					`http://tomimedia.duckdns.org:3000/user/tasks?userId=osztály`
				)
					.then((res1) => res1.json())
					.then(
						(result) => {
							this.setState({
								isLoaded: true,
								hazik: result,
							});
						},
						(error) => {
							this.setState({
								isLoaded: true,
								error,
							});
						}
					);
			});
		}
	}

	render() {
		let listItems = (
			<tr>
				<td></td>
			</tr>
		);
		if (this.state.error) {
			listItems = (
				<tr>
					<td>Hiba: {this.state.error.message}</td>
				</tr>
			);
		} else if (!this.state.isLoaded) {
			listItems = (
				<tr>
					<td>Betöltés...</td>
				</tr>
			);
		} else {
			listItems = this.state.hazik.map((hazi) => (
				<tr key={hazi._id}>
					<td>{hazi._id}</td>
					<td>{hazi.targy}</td>
					<td>{hazi.leiras}</td>
					<td>{hazi.kezdete}</td>
					<td>{hazi.lejarat}</td>
					<td>{hazi.status}</td>
					<td>{hazi.userId}</td>
				</tr>
			));
		}
		return (
			<div>
				<NavigationBar page="masolas" />
				<div className="container mt-5">
					<h2>Házi feladat bemásolása jelenlegi felhasználó alá</h2>
					<h5 className="text-info">
						Jelenlegi felhasználó: {this.state.currentUser}
					</h5>
					{this.state.error !== "" ? (
						<h5 className="text-danger">{this.state.error}</h5>
					) : (
						""
					)}
					<form onSubmit={this.handleSubmit}>
						<input
							type="text"
							name="id"
							id="id"
							placeholder="Házi feladat azonosítója"
							value={this.state.azon}
							onChange={this.handleAzonChange}
							required
							autoComplete="off"
							className="form-control mb-2 bg-light"
						/>
						<input
							type="submit"
							value="Másolás"
							className="btn btn-success mb-5"
						/>
					</form>
					<table className="table table-dark table-striped table-responsive">
						<thead className="thead-dark">
							<tr>
								<th scope="col">ID</th>
								<th scope="col">Tárgy</th>
								<th scope="col">Leírás</th>
								<th scope="col">Kezdete</th>
								<th scope="col">Lejárat</th>
								<th scope="col">Státusz</th>
								<th scope="col">Felh azon</th>
							</tr>
						</thead>
						<tbody>{listItems}</tbody>
					</table>
				</div>
			</div>
		);
	}
}

export default Masolas;
