import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import NavigationBar from "../NavigationBar/NavigationBar";

class Register extends Component {
	state = {
		error: "",
		username: "",
		email: "",
		password: "",
		done: false,
	};
	handleSubmit = (e) => {
		e.preventDefault();
		let { username, password, email } = this.state;
		if (username !== "" && password !== "" && email !== "") {
			let requestOption = {
				method: "POST",
				headers: { "Content-Type": "application/json" },
				body: JSON.stringify({
					uname: this.state.username,
					pwd: this.state.password,
					email: this.state.email,
				}),
			};
			fetch(
				"http://tomimedia.duckdns.org:3002/registerUser",
				requestOption
			)
				.then((res) => res.json())
				.then((data) => {
					if (data.register === "failed") {
						this.setState({
							error: data.error,
							username: "",
							password: "",
							email: "",
						});
					} else {
						this.setState({
							error: "",
							username: "",
							password: "",
							email: "",
							done: true,
						});
					}
				});
		} else {
			this.setState({
				error: "Nem töltötted ki a kötelező mezőket",
				username: "",
				password: "",
				email: "",
			});
		}
	};
	handleUsernameChange = (e) => {
		this.setState({
			username: e.target.value,
		});
	};
	handleEmailChange = (e) => {
		this.setState({
			email: e.target.value,
		});
	};
	handlePasswordChange = (e) => {
		this.setState({
			password: e.target.value,
		});
	};
	render() {
		return (
			<div>
				<NavigationBar page="register" />
				{this.state.done ? (
					<Redirect to="/" />
				) : (
					<div className="container-fluid">
						<div className="row">
							<div className="col-md-3 col-sm-12"></div>
							<div className="col-md-6 col-sm-12">
								{this.state.error !== "" ? (
									<h4 className="mt-5 text-danger">
										{this.state.error}
									</h4>
								) : (
									""
								)}
								<form onSubmit={this.handleSubmit}>
									<div className="form-group">
										<label htmlFor="uname" className="mt-2">
											Felhasználónév
										</label>
										<input
											type="text"
											name="uname"
											id="uname"
											placeholder="Felhasználónév"
											autoComplete="off"
											required
											onChange={this.handleUsernameChange}
											value={this.state.username}
											className="form-control bg-light"
										/>
										<label htmlFor="email" className="mt-2">
											E-Mail
										</label>
										<input
											type="email"
											name="email"
											id="email"
											placeholder="E-Mail cím"
											autoComplete="off"
											required
											onChange={this.handleEmailChange}
											value={this.state.email}
											className="form-control bg-light"
										/>
										<label htmlFor="pwd" className="mt-2">
											Jelszó
										</label>
										<input
											type="password"
											name="pwd"
											id="pwd"
											placeholder="Jelszó"
											autoComplete="off"
											required
											onChange={this.handlePasswordChange}
											value={this.state.password}
											className="form-control bg-light"
										/>
										<input
											type="submit"
											className="btn btn-info mt-1"
											value="Regisztráció"
										/>
									</div>
								</form>
							</div>
							<div className="col-md-3 col-sm-12"></div>
						</div>
					</div>
				)}
			</div>
		);
	}
}

export default Register;
