import React, { Component } from "react";
import Cookies from "js-cookie";
import Axios from "axios";
import NavigationBar from "./Components/NavigationBar/NavigationBar";

class App extends Component {
	state = {
		currentUser: Cookies.get("UsrId"),
		error: null,
		isLoaded: false,
		hazik: [],
	};
	componentDidMount() {
		if (this.state.currentUser === "osztály") {
			Axios.get(
				`https://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
			).then(
				(result) => {
					this.setState({
						isLoaded: true,
						hazik: result.data,
					});
				},
				(error) => {
					this.setState({
						isLoaded: true,
						error,
					});
				}
			);
		} else if (this.state.currentUser === "") {
			Cookies.set("UsrId", "osztály");
			this.setState({
				currentUser: "osztály",
			});
			Axios.get(
				`https://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
			).then(
				(result) => {
					this.setState({
						isLoaded: true,
						hazik: result.data,
					});
				},
				(error) => {
					this.setState({
						isLoaded: true,
						error,
					});
				}
			);
		} else {
			let requestOptions = {
				headers: {
					"Access-Token": this.state.currentUser,
				},
			};
			Axios.get(
				"https://tomimedia.duckdns.org:3002/getCurrentUser",
				requestOptions
			).then((data) => {
				console.log(data);
				this.setState({
					currentUser: data.data.user,
				});
				Axios.get(
					`https://tomimedia.duckdns.org:3000/user/tasks?userId=${this.state.currentUser}`
				).then(
					(result) => {
						this.setState({
							isLoaded: true,
							hazik: result.data,
						});
					},
					(error) => {
						this.setState({
							isLoaded: true,
							error,
						});
					}
				);
			});
		}
	}
	render() {
		// let hazik = [
		// 	{
		// 		_id: "2837462384",
		// 		targy: "Matek",
		// 		leiras: "Valami házi izé",
		// 		kezdete: new Date(Date.now()),
		// 		lejarat: new Date(Date.now()),
		// 		status: "várakozik",
		// 		userId: "osztály",
		// 	},
		// 	{
		// 		_id: "2837462384",
		// 		targy: "Matek",
		// 		leiras: "Valami házi izé",
		// 		kezdete: new Date(Date.now()),
		// 		lejarat: new Date(Date.now()),
		// 		status: "várakozik",
		// 		userId: "osztály",
		// 	},
		// ];
		let listItems = (
			<tr>
				<td></td>
			</tr>
		);
		if (this.state.error) {
			listItems = (
				<tr>
					<td>Hiba: {this.state.error.message}</td>
				</tr>
			);
		} else if (!this.state.isLoaded) {
			listItems = (
				<tr>
					<td>Betöltés...</td>
				</tr>
			);
		} else {
			listItems = this.state.hazik.map((hazi) => (
				<tr key={hazi._id}>
					<td>{hazi._id}</td>
					<td>{hazi.targy}</td>
					<td>{hazi.leiras}</td>
					<td>{hazi.kezdete}</td>
					<td>{hazi.lejarat}</td>
					<td>{hazi.status}</td>
					<td>{hazi.userId}</td>
				</tr>
			));
		}
		return (
			<div className="App">
				<NavigationBar page="fooldal" />
				<main>
					<div className="container mt-5">
						<div className="row">
							<div
								className="col-md-12 col-lg-6"
								alignItems="center"
							>
								<div className="card bg-light">
									<div className="card-body">
										<img
											src={require("./img/me.jpg")}
											className="card-img-top"
											alt="Kép Németh Tamás-ról"
										/>
										<h1 className="card-title text-dark">
											Németh Tamás
										</h1>
										<h5 className="card-subtitle mb-2 text-muted">
											Fejlesztő
										</h5>
										<p className="card-text text-muted">
											A weboldal tulajdonosa
										</p>
									</div>
								</div>
							</div>
							<div
								className="col-md-12 col-lg-6"
								alignItems="center"
							>
								<div className="card bg-light">
									<div className="card-body">
										<h1 className="card-title">
											Házi feladat nyilvántartó
										</h1>
										<h5 className="card-subtitle mb-2 text-muted">
											&copy;Németh Tamás
										</h5>
										<p className="card-text">
											Üdvözöllek a Házi feladat
											nyilvántartó rendszerben.
											<br></br>
										</p>
										<p className="text-muted">
											Ha bármi furcsa viselkedést veszel
											észre az oldallal kapcsolatban,
											nyomj F5-öt. Ez a hibák 90%-át
											megoldja.
										</p>
									</div>
									<ul className="list-group list-group-flush bg-light">
										<li className="list-group-item bg-light"></li>
										<li className="list-group-item bg-light">
											<a
												href="/hozzaadas"
												style={{ width: 250 }}
												className="card-link btn btn-outline-success"
											>
												Házi feladat hozzáadása
											</a>
										</li>
										<li className="list-group-item bg-light">
											<a
												href="/modositasuser"
												style={{ width: 250 }}
												className="card-link btn btn-outline-warning"
											>
												Házi feladat módosítása
											</a>
										</li>
										<li className="list-group-item bg-light">
											<a
												href="/torles"
												style={{ width: 250 }}
												className="card-link btn btn-outline-danger"
											>
												Házi feladat törlése
											</a>
										</li>
									</ul>
								</div>
							</div>
						</div>
						<h2 className="display4 mt-5">Jelenlegi felhasználó</h2>
						<h2 className="display4 mt-2 offset-sm-0 offset-md-4">
							<strong>{this.state.currentUser}</strong>
						</h2>
						<h2 className="display4">Az összes házi feladat:</h2>
						<table className="table table-dark table-striped table-responsive">
							<thead className="thead-dark">
								<tr>
									<th scope="col">ID</th>
									<th scope="col">Tárgy</th>
									<th scope="col">Leírás</th>
									<th scope="col">Kezdete</th>
									<th scope="col">Lejárat</th>
									<th scope="col">Státusz</th>
									<th scope="col">Felh. azon</th>
								</tr>
							</thead>
							<tbody>{listItems}</tbody>
						</table>
					</div>
				</main>
			</div>
		);
	}
}

export default App;
