import ReactDOM from "react-dom";
import React from "react";
import App from "./App";
import NotFound from "./Components/NotFound/NotFound.js";
import { Route, BrowserRouter as Router, Switch } from "react-router-dom";
import Hozzaadas from "./Components/Hozzaadas/Hozzaadas";
import Login from "./Components/Login/Login";
import Logout from "./Components/Logout/Logout.jsx";
import Register from "./Components/Register/Register";
import ForgotPass from "./Components/ForgotPass/ForgotPass";
import JelszoVisszaalitasa from "./Components/JelszoVisszaalitasa/JelszoVisszaalitasa";
import Modositas from "./Components/Modositas/Modositas";
import ModositasUser from "./Components/ModositasUser/ModositasUser";
import Masolas from "./Components/Masolas/Masolas";
import Torles from "./Components/Torles/Torles";
import Cookies from "js-cookie";

if (Cookies.get("UsrId") === undefined) Cookies.set("UsrId", "osztály");

const routing = (
	<Router>
		<div>
			<Switch>
				<Route exact path="/" component={App} />
				<Route path="/index" component={App} />
				<Route path="/hozzaadas" component={Hozzaadas} />
				<Route path="/login" component={Login} />
				<Route path="/logout" component={Logout} />
				<Route path="/register" component={Register} />
				<Route path="/forgotPassword" component={ForgotPass} />
				<Route path="/modositas" component={Modositas} />
				<Route path="/modositasuser" component={ModositasUser} />
				<Route path="/masolas" component={Masolas} />
				<Route path="/torles" component={Torles} />
				<Route
					path="/jelszoVisszaalitasa/:userId"
					component={JelszoVisszaalitasa}
				/>
				<Route component={NotFound} />
			</Switch>
		</div>
	</Router>
);

ReactDOM.render(<div>{routing}</div>, document.getElementById("root"));
