import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import NavigationBar from "../NavigationBar/NavigationBar";

class ForgotPass extends Component {
	state = {
		email: "",
		error: "",
		failed: false,
		done: false,
	};
	handleEmailChange = (e) => {
		this.setState({
			email: e.target.value,
		});
	};
	handleSubmit = (e) => {
		e.preventDefault();
		let requestOption = {
			method: "POST",
			headers: { "Content-Type": "application/json" },
			body: JSON.stringify({
				email: this.state.email,
			}),
		};
		fetch("http://tomimedia.duckdns.org:3002/resetPassword", requestOption)
			.then((res) => res.json())
			.then((data) => {
				console.log(data);
				if (data.reset === "failed") {
					this.setState({
						email: "",
						failed: true,
						error: data.error,
					});
				} else {
					this.setState({
						done: true,
						email: "",
					});
				}
			});
	};
	render() {
		return (
			<div>
				<NavigationBar />
				{this.state.done ? (
					<Redirect
						to={{ pathname: "/login", state: { error: "" } }}
					/>
				) : this.state.failed ? (
					<Redirect
						to={{
							pathname: "/login",
							state: { error: this.state.error },
						}}
					/>
				) : (
					<div className="container-fluid">
						<div className="row">
							<div className="col-md-3 col-sm-12"></div>
							<div className="col-md-6 col-sm-12">
								<h4 className="mt-5 text-info">
									Add meg az Email címed amivel regisztráltál
								</h4>
								<form onSubmit={this.handleSubmit}>
									<div className="form-group">
										<label htmlFor="email" className="mt-2">
											Email cím
										</label>
										<input
											type="email"
											name="email"
											id="email"
											className="form-control"
											autoComplete="off"
											placeholder="Email cím"
											required
											value={this.state.email}
											onChange={this.handleEmailChange}
										/>
										<input
											type="submit"
											value="Elküld"
											className="btn btn-info mt-1"
										/>
									</div>
								</form>
							</div>
							<div className="col-md-3 col-sm-12"></div>
						</div>
					</div>
				)}
			</div>
		);
	}
}

export default ForgotPass;
